export default {
    data() {
        return {
            PageTopOptions: {
                title: '个性主题'
            },
            aTheme: [{
                class: 'default-theme',
                title: '默认主题',
                index: 'default',
                isIconDui: false,
            }, {
                class: 'custom-theme',
                title: '自定义主题',
                index: 'custom',
                isIconDui: false,
            }, {
                class: 'black-theme',
                title: '雅黑',
                index: 'yahei',
                isIconDui: false,
            }, {
                class: 'red-theme',
                title: '海棠红',
                index: 'redbegonia',
                isIconDui: false,
            }, {
                class: 'green-theme',
                title: '藏青',
                index: 'navy',
                isIconDui: false,
            }]
        }
    },
    mounted() {
        for (let item of this.aTheme) {
            item.isIconDui = false;
            if (item.index == document.body.className) {
                item.isIconDui = true;
            }
        }
    },
    methods: {
        backout() {
            this.$router.back(1);
        },
        changeTheme(index) {
            const sThemeId = index;
            this.$exchangeTheme(sThemeId);
            for (let item of this.aTheme) {
                item.isIconDui = false;
                if (item.index == index) {
                    item.isIconDui = true;
                    this.$store.commit('UPDATE_THEME', item.index);
                    // window.localStorage.setItem('oTheme', item.index);
                    sessionStorage.setItem('oTheme', item.index);
                }
            }
        }
    }
}

